import React from 'react';

const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'));

//users route
const UsersList = React.lazy(() => import('./views/users/UsersList'));
//categories route
const CategoriesList = React.lazy(() => import('./views/categories/CategoriesList'));
const CategoriesAdd = React.lazy(() => import('./views/categories/CategoriesAdd'));
//posts route
const PostsList = React.lazy(() => import('./views/posts/PostsList'));
const PostsAdd = React.lazy(() => import('./views/posts/PostsAdd'));
//jobs route
const JobsList = React.lazy(() => import('./views/jobs/JobsList'));
const JobsAdd = React.lazy(() => import('./views/jobs/JobsAdd'));
//events route
const EventsList = React.lazy(() => import('./views/events/EventsList'));
const EventsAdd = React.lazy(() => import('./views/events/EventsAdd'));
//organizations route
const OrganizationsList = React.lazy(() => import('./views/organizations/OrganizationsList'));
const OrganizationsAdd = React.lazy(() => import('./views/organizations/OrganizationsAdd'));
//services route
const ServicesList = React.lazy(() => import('./views/services/ServicesList'));
const ServicesAdd = React.lazy(() => import('./views/services/ServicesAdd'));
//comments route
const CommentsList = React.lazy(() => import('./views/comments/CommentsList'));
//polls route
const PollsList = React.lazy(() => import('./views/polls/PollsList'));
const PollsAdd = React.lazy(() => import('./views/polls/PollsAdd'));
//discussions route
const DiscussionsList = React.lazy(() => import('./views/discussions/DiscussionsList'));
const DiscussionsAdd = React.lazy(() => import('./views/discussions/DiscussionsAdd'));
const RepliesList = React.lazy(() => import('./views/discussions/RepliesList'));
//system route
const AboutUs = React.lazy(() => import('./views/system/AboutUs'));
const Tutorials = React.lazy(() => import('./views/system/Tutorials'));


const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/dashboard', name: 'Dashboard', component: Dashboard },

  { path: '/categories', name: 'List Categories', component: CategoriesList, exact: true },
  { path: '/categories/add', name: 'Add Category', component: CategoriesAdd, exact: true },

  { path: '/posts', name: 'List Posts', component: PostsList, exact: true },
  { path: '/posts/add', name: 'Add Post', component: PostsAdd, exact: true },

  { path: '/jobs', name: 'List Posts', component: JobsList, exact: true },
  { path: '/jobs/add', name: 'Add Post', component: JobsAdd, exact: true },

  { path: '/events', name: 'List Posts', component: EventsList, exact: true },
  { path: '/events/add', name: 'Add Post', component: EventsAdd, exact: true },

  { path: '/users', name: 'List Users', component: UsersList, exact: true },

  { path: '/services', name: 'List Services', component: ServicesList, exact: true },
  { path: '/services/add', name: 'Add Service', component: ServicesAdd, exact: true },

  { path: '/organizations', name: 'List Organizations', component: OrganizationsList, exact: true },
  { path: '/organizations/add', name: 'Add Organization', component: OrganizationsAdd, exact: true },

  { path: '/polls', name: 'List Polls', component: PollsList, exact: true },
  { path: '/polls/add', name: 'Add Poll', component: PollsAdd, exact: true },

  { path: '/discussions', name: 'List Discussions', component: DiscussionsList, exact: true },
  { path: '/discussions/add', name: 'Add Discussion', component: DiscussionsAdd, exact: true },
  { path: '/discussions/replies', name: 'Replies', component: RepliesList, exact: true },

  { path: '/comments', name: 'List Comments', component: CommentsList, exact: true },

  { path: '/system/aboutus', name: 'About HyperDev', component: AboutUs, exact: true },
  { path: '/system/tutorials', name: 'Tutorials', component: Tutorials, exact: true },
];

export default routes;
