import GetUrl from '../server/ApiEndpoints'
let bearerToken = 'Bearer ' + localStorage.getItem('HDI-token')

const SignIn = async function (username, password) {
    return fetch(GetUrl('SignIn'), {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({ email: username, password: password })
    })
        .then(response => {
            return response
        })
        .catch(error => {
            console.log(error)
        })
}

const Logout = async function () {
    return fetch(GetUrl('Logout'), {
        method: 'POST',
        headers: {
            'Authorization': bearerToken,
            'Content-Type': 'application/json'
        }
    })
        .then(response => {
            return response
        })
        .catch(error => {
            console.log(error)
        })
}


export { SignIn, Logout }