import React from 'react'
import {
  CBadge,
  CDropdown,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
  CImg
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { freeSet } from '@coreui/icons'
import { Logout } from '../server/AuthManagement'

const generateIcon = function (iconName) {
  return (<CIcon content={freeSet[iconName]} className="mfe-2" />)
}
const TheHeaderDropdown = ({ setToken }) => {
  const userInfo = JSON.parse(localStorage.getItem('HDI-UI'))
  const handleLogout = async () => {
    const response = await Logout()
    setToken('')
    localStorage.removeItem('HDI-UI')
    if (!response.ok) {
      console.log('Login failed!')
    }
  }
  return (
    <CDropdown
      inNav
      className="c-header-nav-items mx-2"
      direction="down"
    >
      <CDropdownToggle className="c-header-nav-link" caret={false}>
        <div className="c-avatar">
          <CImg
            src={'avatars/user.png'}
            className="c-avatar-img"
            alt="admin@bootstrapmaster.com"
          />
        </div>
        <span className="ml-2">Hello <strong>{userInfo.firstName + ' ' + userInfo.lastName}!</strong></span>
      </CDropdownToggle>
      <CDropdownMenu className="pt-0" placement="bottom-end">
        <CDropdownItem>
          {generateIcon('cilUser')}
          Profile
        </CDropdownItem>
        <CDropdownItem onClick={handleLogout}>
          {generateIcon('cilAccountLogout')}
          Logout
        </CDropdownItem>
      </CDropdownMenu>
    </CDropdown>
  )
}

export default TheHeaderDropdown
